.contact-container {
    grid-template-columns: repeat(2, max-content);
    justify-content: center;
    column-gap: 6rem;
    padding-bottom: 3rem
}

.contact-title {
    text-align: center;
    font-size: var(--h3-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-1-5);
}

.contact-info {
    display: grid;
    row-gap: 1rem;
    grid-template-columns: 300px
}

.contact-card {
    background-color: var(--container-color);
    border: 1px solid rgba(0,0,0,.1);
    padding: 1rem;
    border-radius: .75rem;
    text-align: center
}

.contact-card-icon {
    font-size: 2rem;
    color: var(--title-color);
    margin-bottom: var(--mb-1-5)
}

.contact-card-title, .contact-card-data {
    font-size: var(--small-size)
}

.contact-card-title {
    font-weight: var(--font-medium)
}

.contact-card-data {
    display: block;
    margin-bottom: var(--mb-0-75)
}

.contact-button {
    color: var(--text-color);
    font-size: var(--small-size);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    column-gap: .25rem
}

.contact-button-icon {
    margin-bottom: -3px;
    transition: .5s;
}

.contact-button:hover .contact-button-icon {
    transform: translate(.25rem)
}

.send-button-icon {
    transform: rotate(90deg); 
    margin-left: 5px;
    margin-top: 2px;
    transition: .5s
}

.send-button:hover .send-button-icon {
    transform: translateX(.35rem) translateY(-.3rem) rotate(70deg)
}

.contact-form {
    width: 360px
}

.contact-form-div {
    position: relative;
    margin-bottom: var(--mb-2);
    height: 4rem;
}

.contact-form-input {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 2px solid rgba(0,0,0,.3);
    background: none;
    outline: none;
    color: var(--text-color);
    border-radius: .75rem;
    padding: 1.5rem;
    z-index: 1;
}

.contact-form-tag {
    position: absolute;
    top: -.75rem;
    left: 1.25rem;
    font-size: var(--smaller-size);
    padding: .25rem;
    background-color: var(--body-color);
    z-index: 10
}

.contact-form-area {
    height: 11rem;
}

.contact-form-area textarea {
    resize: none;
}

/* RESPONSIVE BREAKPOINT */
@media screen and (max-width: 992px) {
    .contact-container {
        column-gap: 3rem;
    }
}

@media screen and (max-width: 768px) {
    .contact-container {
        grid-template-columns: 1fr;
        row-gap: 3rem;
    }
    .contact-info {
        justify-content: center
    }
    .contact-form {
        margin: 0 auto;
    }
}

@media screen and (max-width: 576px) {
    .contact-info {
        grid-template-columns: 1fr
    }
    .contact-form {
        width: 100%
    }
}

@media screen and (max-width: 420px) {    
    .contact-content:nth-child(1) {
        margin-top: -1.5rem
    }
}